.form{
    position: relative;
    width: 377px;
    height: auto;
    background: #2D334D;
    box-shadow: 0px 2px 15px rgba(0, 0, 0, 0.15);
    border-radius: 5px;
    padding-top: 35px;
    padding-bottom: 39px;
}

.form_name{
    margin-bottom: 26px; 
    font-family: Roboto;
    font-style: normal;
    font-weight: 500;
    font-size: 25px;
    text-align: center;
}

.form_user_name{
    text-align: center;
}

.form_user_phone_number{
    text-align: center;
}

.form_user_email{
    text-align: center;
}

.form_user_password{
    text-align: center;
}

.form input{
    font-family: Roboto;
    font-style: normal;
    font-weight: normal;
    color: white;
    width: 275px;
    height: 57px;
    margin-left: 35px;
    margin-right: 35px;
    padding-left: 30px;
    margin-bottom: 20px;
    background: rgba(166, 169, 202,0.3);
    border-radius: 3px; 
    border: none;
}

.form_button{
    text-align: center;
}

.form_button button{
    color: white;
    background: #FF8A01;
    border-color: #FF8A01;
    border-radius: 3px;
    max-height: 57px;
    height: 57px;
    width: 307px;
    padding: 0;
    font-size: 16px;
}

.form_checkbox_gender{
    margin-left: 35px;
    margin-bottom: 35px;
}

.form_checkbox_gender input[type='checkbox']{
    margin:0;
    width: 16px;
    height: 16px;
}
.form_checkbox_gender label{
    margin-right: 25px;
}

.form_checkbox_gender input[type='checkbox']+label{
    margin-left: 11px;
}

.form_link{
    margin-top: 28px;
    text-align: center;
}

.form_link a{
    text-decoration: none; 
    color: #8589AC;
}

.form_link a span{
    color: #43A9E7;
}

@media(max-width: 966px){
    .form_name{
        margin-left: 38px;
        text-align: left;
    }
}

@media(max-width: 400px){
    .form{
        width: 279px;
        border-radius: 3px;
        padding-top: 40px;
        padding-bottom: 23px;
    }
    
    .form_name{
        margin-bottom: 26px;
        margin-left: 21px;
        font-family: Roboto;
        font-size: 20px;
        text-align: left;
    }

    .form input{
        width: 213px; /*20px съел padding */
        height: 50px;
        font-size: 13px;
        padding-left: 20px;
        margin-left: 23px;
        margin-right: 23px;
        margin-bottom: 10px;
    }
    
    .form_checkbox_gender{
        margin-left: 23px;
        margin-bottom: 37px;
    }

    .form_checkbox_gender label{
        margin-right: 25px;
    }

    .form_checkbox_gender input[type='checkbox']+label{
        margin-left: 11px;
    }
    
    .form_button{
        margin-top: 26px;
        text-align: center;
    }
    
    .form_button button{
        padding: 0;
        color: white;
        height: 55px;
        width: 233px;
    }
    
    .from_forgot_password {
        margin-top: 18px;
        text-align: left;
        padding-left: 22px;
        font-size: 12px;
    }
    
    .from_forgot_password a{
        color:#43A9E7;
        text-decoration: none; 
        text-align: center;
    }
    
    .from_forgot_password a:active{
        color:#43A9E7;
    }
    
    .form_link{
        margin-top: 28px;
        text-align: center;
    }
    
    .form_link a{
        font-size: 12px;
        text-decoration: none; 
    }
    .form_link a span{     
        color: #43A9E7;
    }
}   